import { Component, OnInit } from '@angular/core';
import { TaskService } from 'src/app/Services/task.service';

@Component({
  selector: 'app-services-test',
  templateUrl: './services-test.component.html',
  styleUrls: ['./services-test.component.scss']
})
export class ServicesTestComponent implements OnInit {

  constructor(private taskService: TaskService) { }

  ngOnInit(): void {

  }

  tasks;

  addNewTask() {
    this.taskService.addNewTask();
  }

  readTask() {
    this.taskService.readTasksList().subscribe((task) => {
      this.tasks = task;
    })
  }

}
