import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'rfr-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss']
})
export class ButtonComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  @Input() type = BtnType.positive; // gradient, positive, disabled, neutral1, neutral2gradientTxt, neutral3, neutral4, neutral5, neutral6
  @Input() oval = BtnOval.partial; // oval-circle, oval-medium, oval-partial, oval-none
  @Input() size = BtnSize.medium; // size-small, size-medium, size-big, size-small-medium , size-medium-big
  @Input() font = BtnFont.medium; // font-small, font-medium, font-big
  @Input() fontBold = FontBold.normal; // fontBold-normal, fontBold-medium, fontBold-big
  @Input() fontPadding = FontPadding.normal; // fontPadding-normal fontPadding-medium, fontPadding-big
  @Input() icon = null; // name of icon
  @Input() loading = false; // true or false

}

export enum BtnType {
  gradient = 'gradient',
  positive = 'positive',
  disabled = 'disabled',
  neutral1 = 'neutral1',
  neutral2gradientTxt = 'neutral2gradientTxt',
  neutral3 = 'neutral3',
  neutral4 = 'neutral4',
  neutral5 = 'neutral5',
  neutral6 = 'neutral6'
}

export enum BtnOval {
  circle = 'oval-circle',
  medium = 'oval-medium',
  partial = 'oval-partial',
  none = 'oval-none'
}

export enum BtnSize {
  small = 'size-small',
  medium = 'size-medium',
  big = 'size-big',
  smallMedium = "small-medium",
  mediumBig = "medium-big"
}

export enum BtnFont {
  small = 'font-small',
  medium = 'font-medium',
  big = 'font-big'
}

export enum FontBold {
  normal = 'fontBold-normal',
  medium = 'fontBold-medium',
  big = 'fontBold-big'
}

export enum FontPadding {
  normal = 'fontPadding-normal',
  medium = 'fontPadding-medium',
  big = 'fontPadding-big'
}
